<template>
	<div id="NoteContainer" :class="{focused:isFocused}">
		<NoteInstance class="instance" :noteid="0"/>
	</div>
</template>

<script>
import {mapState, mapGetters} from 'vuex';
import NoteInstance from './NoteInstance.vue'
import helperfns from '../helperfns.js'
import {Note} from '../store.js'

export default {

	name: 'NoteContainer',
	
	components: {
		NoteInstance
	},

	data(){
		
		return {
		
			
			_selectedInstances : [this],//noteInstance functions act on these when user invokes
			_focusedInstance: this,//instance with cursor
			noteContainer: this,
			copiedInstances: []
		}
	},

	methods: {
		select(instance,appendMode=false){
			if (!appendMode){ this.selectedInstances = []}
			this.selectedInstances = [...this.selectedInstances, instance];
		},

		focus(instance,appendMode=false){
			this.focusedInstance = instance;
			this.select(instance,appendMode);
		},

		clearSelectedInstances(){this.selectedInstances = []},

		getNewNote(){ 
			//Get a new Note
			var nextID = this.$store.getters.nextID;
			this.$store.commit('registerNewNote',nextID);
			var newNote = this.$store.getters.getNoteByID(nextID);
			return newNote;
		},

		swapInstances(i1,i2){
			//Swap the children of the instances
			var parent1 = i1.$parent.note; 
			var indx1 = parent1.children.indexOf(i1.note.id);
			var parent2 = i2.$parent.note; 
			var indx2 = parent2.children.indexOf(i2.note.id);
			parent1.children[indx1] = i2.note.id;
			parent2.children[indx2] = i1.note.id;

			var i1BtnState = [];
			var i2BtnState = [];
			var recordBtnStates = function(instance,ar){
				ar[0]=instance.button_text;
				instance.$children.forEach(function(childInst){
					var childar = [];
					ar.push(childar);
					recordBtnStates(childInst,childar);
				})

			}
			
			recordBtnStates(i1,i1BtnState);
			console.log(i1BtnState);
			recordBtnStates(i2,i2BtnState);
			console.log(i2BtnState);
			
			i1.$parent.$forceUpdate();
			if(i1.$parent != i2.$parent)
			i2.$forceUpdate();
			
			//TODO
			var loadBtnStates=function(instance,ar){
				debugger;
				instance.button_text=ar[0];
				instance.$parent.$forceUpdate();
				for(var i=1;i<ar.length;i++){
					var children = instance.$children[i-1];
					var arIn = ar[i];
					instance.$nextTick( function(){loadBtnStates(children,arIn)} );
				}
			}

			//loadBtnStates(i2,i1BtnState);
			//loadBtnStates(i1,i2BtnState);
		}

	},
 
	computed: {
	
		selectedInstances : {
			set(instances){
				this.$data._selectedInstances = instances;	
			},
			get(){
				return this.$data._selectedInstances;
			}
		},

		focusedInstance: {
			set(instance){
				this.$data._focusedInstance = instance;
			},

			get(){
				return this.$data._focusedInstance;
			}
		},

		showMarkup : {
			set(value){this.$store.state.settings.showMarkup = value},
			get(){return this.$store.state.settings.showMarkup}
		},

		isFocused(){
			return this.focusedInstance == this.$children[0];
		}

		

	},

	created(){
		document.addEventListener('keydown', e => {
			switch(e.key){
				case 'Enter':
					if(!this.focusedInstance.isEditable){
						this.focusedInstance.isEditable=true;
						e.preventDefault();
					}
					
					else{
						this.focusedInstance.$nextTick(function(){
							var fta=this.focusedInstance.textarea;
							// If a textarea is being edited and Enter is clicked, add <br/> to the markup"
							fta.value = fta.value.slice(0,fta.selectionStart) + "<br/>" + fta.value.slice(fta.selectionStart);
						}.bind(this));
					}
					break;

				case "Alt":
					e.preventDefault();
					if(this.focusedInstance.isEditable){
						this.focusedInstance.selectionStart = this.focusedInstance.textarea.selectionStart;
						this.focusedInstance.isEditable=false;
					}
					break;

				case 'Insert':
					e.preventDefault();
					if(this.focusedInstance.isEditable){return}
					var newNote = this.getNewNote();
					this.selectedInstances.forEach(function(instance){
						instance.addNote(newNote);
						instance.expand();
					});
					//Focus newNote under focused instance
					this.$nextTick(function(){
						this.focusedInstance.$children.filter(x=>x.note==newNote)[0].focus()
						}.bind(this));
					
					break;

				case "ArrowUp":
				case "ArrowDown":
					if(this.focusedInstance.isEditable){break;}
					if (e.shiftKey && e.ctrlKey){break}
					e.preventDefault();

					var fi = this.focusedInstance;

					var instanceToActOn; 
					if (e.key=="ArrowUp")	{instanceToActOn = fi.getPreviousVisibleInstance();}
					if (e.key=="ArrowDown")	{instanceToActOn = fi.getNextVisibleInstance();}

					if (!e.shiftKey){
						this.focus(instanceToActOn,e.ctrlKey)
					}
					if (e.shiftKey){
						//Swap fi with it's adjacent sibling, if it exists 
						if (e.key == "ArrowUp"){ instanceToActOn = fi.previousSibling}
						if (e.key == "ArrowDown"){ instanceToActOn = fi.nextSibling} 

						if (instanceToActOn != null){
							this.swapInstances(fi,instanceToActOn);
							instanceToActOn.focus();
						}
					}
					break;

				case "Tab":
					e.preventDefault();
					this.selectedInstances.forEach(function(instance){
						instance.toggleButton();
					})
					break;

				case "Delete":
					e.preventDefault();

					//can't delete first instance by design 
					if(this.$parent==this.noteContainer && this.indexAsSibling==0){break}

					

					this.selectedInstances.forEach(function(instance){
						instance.removeFromParent();
					});
					
					var fi = this.focusedInstance;
					var pathCopy = fi.path.map(x=>x)
					pathCopy.pop(); //Remove "this" from it's own path
					var nextAncestor
			
					while(pathCopy.length>0){
						 nextAncestor = pathCopy.pop();
						 if(nextAncestor.isVisible){
						 	console.log(nextAncestor);
						 	this.selectedInstances = [];
						 	this.focus(nextAncestor)
						 	break;
						 }
					}
					break;

				case "c":
					if(!this.focusedInstance.isEditable && e.ctrlKey){
						this.copiedInstances = this.selectedInstances.filter(x=>true);
					}
					break;

				case "v":
					if(!this.focusedInstance.isEditable && e.ctrlKey){
						this.selectedInstances.forEach(function(instance){
							this.copiedInstances.forEach(function(instanceToCopy){
								instance.addNote(instanceToCopy.note);
							})
						}.bind(this))
					}
					break;

			}
		});
	},

	mounted(){
	},

};

</script>

<style scoped>
	
	#NoteContainer {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%; 
		background-color: grey;
		border: 1px solid black;
		margin-top: 3px;
		overflow: auto;
		padding: 1px;
	}

	#NoteContainer.focused { 
	    border: 2px solid red;
	}

</style>