<template>
	<div id="root" :class="{shiftRight:parentIsNotRoot}">
		<div 
		id="topLine"
		ref="topLine"
		:class="{flex:true, border:true, selected:isSelected, focused:isFocused, hidden:isRoot}" 
		@click="focus"
		>
			<button
			@click="toggleButton"
			:class="{boldText:hasChildren}"
			> 
				{{button_text}} 
			</button>
			
			<textarea 
			ref="textarea"
			type="text" 
			v-show="isEditable"
			class="contentBox"
			v-model:value="markup"
			>	
			</textarea>

			<span 
			id="contentSpan"
			ref="contentSpan"
			v-show="!isEditable"
			v-html="markup"
			class="contentBox">
			</span>
		</div>
		
		<keep-alive>
			<div v-if="isExpanded" id="children">
				<NoteInstance  v-for="child in children" :noteid="child"/>
			</div>
		</keep-alive>

	</div>
</template>


<script>
import {mapState, mapGetters} from 'vuex'

export default {

	name: 'NoteInstance',
	
	components: {
	},

	props:{
		noteid: Number
	},

	data(){
		
		return {
			
			noteContainer: this.$parent.noteContainer,
			downArrow: 'Ë…',
			rightArrow: '>',
			button_text: this.downArrow,
			isEditable: false,
			selectionStart: 0
		}
	},

	methods: {
		resize(){
			var tls = this.$refs.topLine.style;
			var contentBox = this.isEditable ? this.$refs.textarea : this.$refs.contentSpan
			
			tls.height = 0;
			tls.height = contentBox.scrollHeight + "px";
		},

		focus(event,appendMode=false){
			if(event && !event.ctrlKey){this.noteContainer.clearSelectedInstances();}
			this.noteContainer.focus(this,appendMode);
		},
		
		addNote(note){
			this.$store.commit("addChildToParent",{child: note, parent: this.note});
			this.expand();
		},

		//getPreviousVisibleInstance, getNextVisibleInstance, and getLastChild are getters because if they
		//are properties the property doesn't update on some shift+up/down-s 
		getPreviousVisibleInstance(){
			if (this.$parent == this.noteContainer && this.indexAsSibling == 0){return this}

			if (this.previousSibling) {
				var lastvi = this.previousSibling; 
				while(lastvi.isExpanded && lastvi.hasChildren)
					{lastvi = lastvi.$children.length>0?lastvi.$children[lastvi.$children.length-1]:null}
				return lastvi;
			}
			else{
				return this.$parent
			}
		},

		getNextVisibleInstance(){
			
			//If this is expanded and has a child, return it
			if(this.isExpanded && this.$children.length>0){return this.$children[0]}

			var recFn = function(instance){
				if (instance.isRoot){return instance}
				var nextSib = instance.getNextSibling(); 
				if (nextSib != null){return nextSib}
				return recFn(instance.$parent);
				
			}

			var ret=recFn(this);
			return ret.isRoot ? this : ret;
		},

		getPreviousSibling(){return this.indexAsSibling>0 ? this.$parent.$children[this.indexAsSibling-1] : null},
		getNextSibling(){return this.indexAsSibling==this.$parent.$children.length-1?null:this.$parent.$children[this.indexAsSibling+1]},
			

		getLastChild(){return this.$children.length>0?this.$children[this.$children.length-1]:null},

		toggleButton(){this.button_text = this.button_text == this.rightArrow ? this.downArrow : this.rightArrow},
		expand(){this.button_text = this.downArrow},
		collapse(){this.button_text = this.rightArrow},
		removeFromParent(){this.$store.commit("removeChildFromParent",{child: this.note, parent: this.$parent.note})}
	},
 
	computed: {
		...mapState({
			children(state){
				return state.notes[this.noteid].children;
			},

			note(state){
				return state.notes[this.noteid];
			}
		}),

		markup: {
			set(markup){
				this.$store.commit('setMarkup',{note:this.note, markup});
				this.resize();

			},
			get(){return this.note.markup}
		},

		//Array of instances from (noteContainer,this]
		path(){
			if (this.$parent == this.noteContainer){return [this]}
			return [...this.$parent.path,this]
		},

		isVisible(){
			if (this.$parent == this.noteContainer){return true}
			if (this.$parent.isVisible && this.$parent.isExpanded){return true}
			return false
		},

		isSelected(){ return this.noteContainer.selectedInstances.includes(this)},
		isFocused(){ return this.noteContainer.focusedInstance == this},
		isExpanded(){ return this.button_text == this.downArrow},
		indexAsSibling(){return this.$parent.$children.indexOf(this)},
		previousSibling(){return this.indexAsSibling>0 ? this.$parent.$children[this.indexAsSibling-1] : null},
		nextSibling(){return this.indexAsSibling==this.$parent.$children.length-1?null:this.$parent.$children[this.indexAsSibling+1]},
		textarea(){return this.$refs.textarea},
		hasChildren(){return this.note.children.length>0},
		isRoot(){return this.$parent == this.noteContainer},
		parentIsNotRoot(){return !this.$parent.isRoot}
	},

	watch: {
		isEditable : function(){
			this.$nextTick(function(){
				this.resize();
				if(this.textarea){this.textarea.focus()}
			});
			
			
		}
	},

	mounted(){
		this.resize();
		this.button_text = this.rightArrow;
		
		//Autoexpand root and autofocus it's first child 
		if (this.isRoot){this.button_text = this.downArrow}
		if(this.$parent.isRoot && this.indexAsSibling==0){this.focus()}
	}

};

</script>
<style scoped>

	button {
			padding: 0;
			border: 0;
			padding: 3px;
			border-right: 1px solid black;
		}

	textarea {
		width: 100%;
		overflow-y: hidden;
	}

	#contentSpan {
		padding-left: 2px;
		min-height: 17px;
	}


	.shiftRight{
		margin-top: 1px;
		float: right;
		width: 99%;

	}

	#topLine{
		height: auto;
		overflow-y: hidden;
	}

	.flex {
		display: flex;
		flex-basis: min-content;
	}

	.contentBox {
		background: white;
		width: 100%;
	}


	.border {
		border: 1px solid black;
	}


	.selected { 
	    border-color: #4d94ff;
	    border-width: 2px;
	}

	.focused { 
	    border-color: red;
	    border-width: 2px;
	}

	.boldText{
		font-weight: bold
	}

	.hidden {
		display: none;
	}

</style>