import Vue from 'vue'
import Vuex from 'vuex'
import helpers from './helperfns.js'

Vue.use(Vuex)

export class Note {
	Hi(){
		console.log("hi");
	}
	constructor(id,markup='',children=[],parents=[]){
		this.id = id;
		this.markup = markup;
		this.children = children;
		this.parents = parents;
	}
}

export default new Vuex.Store({
  state: {
		notes : {

			0 : {
					id : 0,
					markup : "",
					children : [1],
					parents : []
				},

			1 : {
					id : 1,
					markup : "",
					children : [],
					parents : [0]
				}
		},

		settings: {
			showMarkup: false
		}
	},

	getters: {
		testGetter: (state) => {
			console.log(this);
			console.log("testGetter");
			return 1;
		},

		nextID: (state) => {
			return Object.keys(state.notes).length;
		},

		getNoteByID: (state) => (id)=>state.notes[id],
		
	},

	mutations: {
		testMutation(state){console.log("testMutation")},

		setMarkup(state, {note, markup}){
			note.markup = markup;
		},


		registerNewNote(state, id){
			var newNote = new Note(id);
			state.notes = {...state.notes, [id]:newNote};

		},

		addChildToParent(state, {child, parent}){
			parent.children.push(child.id);
			child.parents.push(parent.id);
		},

		removeChildFromParent(state,{child, parent}){ 
				parent.children = parent.children.filter(candidate => candidate!=child.id);
		},

		loadNotes(state,{new_notes, file_name}){
			var old_root = new_notes[0];
			var new_root = state.notes[0];
			state.notes = helpers.mergeNotes(state.notes,new_notes);
			this.commit('addChildToParent',{child:new_root,parent:old_root});
		}

	},
})
